* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    #root {
        max-width: 630px;
        width: 100%;
        margin: auto;
        background: linear-gradient(0deg, #0062cc, #0abcce);
        box-shadow: 5px 5px 150px rgba(#000, .15);
        border-radius: 2rem;
        padding: 2rem;
        // max-height: 630px;
        height: 100%;
        font-family: 'Montserrat';

        h2 {
            text-transform: uppercase;
            font-weight: 600;
        }

        .delo {
            color: #ff0099;
            font-family: monospace;
            font-size: 2.5rem;
            text-shadow: 2px 2px #fff;
        }

        .logo {
            img {
                height: 3rem;
                width: auto;
            }
        }
    }
}